import {
  useLoaderData,
  Link,
  useSubmit,
  Form,
  Outlet,
  useLocation,
} from "@remix-run/react";
import connectDb from "~/db/connectDb.server.js";
//import { requireUserSession } from "~/sessions.server";

export async function loader({ request }) {
  const db = await connectDb();
  //const session = await requireUserSession(request);
  const url = new URL(request.url);
  const searchQuery = url.searchParams.get("q");
  const sortQuery = url.searchParams.get("s");
  const posts = await db.models.Post.find(
    searchQuery
      ? {
          title: { $regex: new RegExp(searchQuery, "i") },
        }
      : {}
  ).sort(
    sortQuery
      ? { [sortQuery]: -1, title: 1 }
      : {
          title: 1,
        }
  );
  //if (session.has("userId")) {
  return posts;
  // }
}

export default function Index() {
  const posts = useLoaderData();
  const submit = useSubmit();
  const location = useLocation();

  return (
    <div className="sm:flex:none md:flex w-full pt-20">
      <div className="shadow-lg bg-gray-100 mr-5 rounded-2xl p-10  sm:w-full md:w-3/4">
        <h1 className="text-2xl mb-4">Explore</h1>
        <h2 className="text-lg mb-3">Den her skal rettes til:</h2>

        <Form
          className="searchForm"
          method="get"
          onChange={(e) => {
            submit(e.currentTarget);
          }}
          action=""
        >
          <input
            placeholder="Search..."
            autoComplete="off"
            type="search"
            name="q"
            className="mb-10 rounded-2xl p-2"
          />
        </Form>

        <Form
          className="sortForm"
          method="get"
          onChange={(e) => {
            submit(e.currentTarget);
          }}
          action=""
        >
          <select className="mb-10 rounded-2xl p-2" name="s" id="s">
            <option value="title">Title</option>
            <option value="description">Description</option>
            <option value="favorite">Favorite</option>
            <option value="timeCreated">Time created</option>
          </select>
        </Form>

        <ul className="">
          {posts.map((post) => {
            return (
              <li
                className="text-blue-600 hover:underline  py-2 text-xl"
                key={post._id}
              >
                <Link
                  to={`/posts/${post._id}${location.search}`}
                  className="focus:font-bold p-1"
                >
                  {post.card}
                </Link>
              </li>
            );
          })}
        </ul>
      </div>
      <div className="shadow-lg bg-gray-100 rounded-2xl p-10 sm:w-full md:w-1/4">
        <Outlet />
      </div>
    </div>
  );
}
